@charset "UTF-8";
* {
  padding: 0;
  margin: 0;
  border: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
  border-radius: 0;
}

textarea {
  resize: none;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
  background-color: transparent;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: unset;
}

/*--------------------*/
body.lock {
  touch-action: none;
  -ms-touch-action: none;
  -webkit-overflow-scrolling: touch;
  overflow: hidden;
}

input[type=number] {
  -moz-appearance: textfield;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

svg path {
  transition: 0.3s;
}

.scrollbar-none {
  -ms-overflow-style: none; /* IE и Edge */
  scrollbar-width: none; /* Firefox */
}
.scrollbar-none::-webkit-scrollbar {
  display: none;
  -webkit-appearance: none;
  width: 0;
  height: 0;
  background: transparent;
}

[data-tab] {
  display: none;
}
[data-tab].active {
  display: block;
}

body {
  font-family: "Open Sans", sans-serif;
  --header: 161px;
  --container: calc((100vw - 1240px) / 2);
  --sectionMargin: 160px;
}
@media (max-width: 1440px) {
  body {
    --container: 100px;
  }
}
@media (max-width: 1024px) {
  body {
    --header: 101px;
    --container: 48px;
    --sectionMargin: 100px;
  }
}
@media (max-width: 568px) {
  body {
    --container: 16px;
    --header: 100px;
    --sectionMargin: 56px;
  }
}

.collapse:not(.collapse_show) {
  display: none;
}

.wrapper {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-weight: 500;
  color: #323137;
}

.main {
  flex: 1;
  padding-top: var(--header);
}

@media (max-width: 1024px) {
  .pc {
    display: none;
  }
}
@media (max-width: 568px) {
  .pc {
    display: none;
  }
}

.tablet {
  display: none;
}
@media (max-width: 1024px) {
  .tablet {
    display: block;
  }
}
@media (max-width: 568px) {
  .tablet {
    display: none;
  }
}

.mobile {
  display: none;
}
@media (max-width: 1024px) {
  .mobile {
    display: none;
  }
}
@media (max-width: 568px) {
  .mobile {
    display: block;
  }
}

.pc-tablet {
