.app-input {
  height: 52px;
  width: 100%;
  border: 2px solid #dcdcde;
  border-radius: 4px;
  padding-left: 16px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0%;
  color: #37363b;
  transition: 0.4s;
}
.app-input:focus {
  border-color: #845186;
}
@media (hover: hover) {
  .app-input:hover {
    border-color: #9d749e;
  }
}
.app-input.transparent {
  border: 0 !important;
  border-bottom: 1px solid #323137 !important;
}

.btn {
  height: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  padding: 0 18px;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  letter-spacing: 0%;
  vertical-align: middle;
  color: #fff;
  border-radius: 8px;
  background-color: #845186;
  transition: 0.4s;
}
@media (max-width: 568px) {
  .btn {
    font-size: 16px;
  }
}
@media (hover: hover) {
  .btn:hover {
    background-color: #9d749e;
  }
}
.btn:active {
  background-color: #845186;
}
.btn:disabled, .btn.disabled {
  background-color: #b9b9bd;
  color: #37363b;
}
.btn.white {
  background-color: #fff;
  border: 2px solid #845186;
  color: #845186;
}
@media (hover: hover) {
  .btn.white:hover {
    border: 2px solid #9d749e;
    color: #9d749e;
  }
}
.btn.white.disabled {
  border-color: #b9b9bd;
  color: #323137;
}

.container {
  margin: 0 auto;
  padding: 0 var(--container);
}

.app-select {
  max-width: unset;
  width: 100%;
  position: relative;
}
.app-select img {
  display: none;
}
.app-select.open {
  z-index: 20;
}
.app-select.open .select__content {
  opacity: 1;
  pointer-events: all;
}
.app-select.open .select__back {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
}
.app-select .select__title {
  position: relative;
  z-index: 1;
  transition: 0.3s;
  cursor: pointer;
  border-radius: 4px;
  background-color: white;
  width: 100%;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 8px;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0%;
}
.app-select .select__title span {
  pointer-events: none;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.app-select .select__title svg {
  width: 24px;
  height: 24px;
  flex: 0 0 24px;
  transition: 0.3s;
  transform-origin: center;
  pointer-events: none;
}
.app-select .select__title button {
  width: 16px;
  height: 16px;
  flex: 0 0 16px;
  display: none;
}
.app-select .select__content {
  padding: 8px 0;
  transition: 0.3s;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 100%;
  translate: 0 2px;
  left: 0;
  border-radius: 8px;
  background-color: white;
  width: 100%;
}
.app-select .select__list {
  display: flex;
  flex-direction: column;
  max-height: 200px;
  overflow: auto;
}
.app-select .select__item {
  cursor: pointer;
  color: #323137;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0%;
  display: flex;
  align-items: center;
  gap: 8px;
  position: relative;
  padding: 12px 16px;
  transition: 0.3s;
  font-weight: 400;
  font-size: 16px;
  line-height: 20px;
  letter-spacing: 0%;
}
@media (hover: hover) {
  .app-select .select__item:hover {
    background-color: rgba(132, 81, 134, 0.4);
  }
}

.phone-select img {
  display: block;
}

.header {
  height: var(--header);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 100;
  background-color: white;
}
.header__upper {
  height: 57px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1024px) {
  .header__upper {
    height: 41px;
    border-bottom: 1px solid var(--input-stroke-dark, #dcdcde);
    margin: 0 calc(-1 * var(--container));
    padding: 0 calc(1 * var(--container));
    width: 100vw;
  }
}
@media (max-width: 568px) {
  .header__upper {
    height: 40px;
  }
}
.header__lang {
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 2%;
  color: #323137;
}
.header__lang > span {
  margin-left: 4px;
  margin-right: 1px;
}
@media (max-width: 568px) {
  .header__lang > span {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0%;
    vertical-align: middle;
  }
}
.header__phone {
  font-weight: 400;
  font-size: 20px;
  line-height: 126%;
  letter-spacing: 3%;
  text-align: right;
  color: #323137;
}
@media (max-width: 568px) {
  .header__phone {
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 0%;
  }
}
.header__down {
  height: 108px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (max-width: 1024px) {
  .header__down {
    height: 60px;
  }
}
.header__logo img,
.header__logo svg {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (max-width: 1024px) {
  .header__logo {
    flex: 0 0 75px;
  }
}
@media (max-width: 568px) {
  .header__logo {
    flex: 0 0 83px;
  }
}
.header__list {
  display: flex;
  align-items: center;
  gap: 24px;
}
@media (max-width: 1440px) {
  .header__list {
    gap: calc(13px + 11 * (100vw - 1024px) / 416);
  }
}
.header__item {
  display: flex;
  align-items: center;
  gap: 4px;
}
.header__item > a {
  cursor: pointer;
  font-weight: 400;
  font-size: 20px;
  line-height: 24px;
  letter-spacing: 3%;
  color: #323137;
}
@media (max-width: 1440px) {
  .header__item > a {
    font-size: calc(14px + 6 * (100vw - 1024px) / 416);
  }
}
.header__item > a.bold {
  font-weight: 700;
}
@media (hover: hover) {
  .header__item:hover .header-services {
    opacity: 1;
    pointer-events: all;
  }
}
.header__btn {
  height: 48px;
  padding: 0 24px;
}
@media (max-width: 1440px) {
  .header__btn {
    height: calc(32px + 16 * (100vw - 1024px) / 416);
    padding: 0 calc(4px + 20 * (100vw - 1024px) / 416);
    font-size: 0 calc(16px + 8 * (100vw - 1024px) / 416);
  }
}
.header__burger {
  width: 30px;
  height: 20px;
  flex: 0 0 30px;
  position: relative;
}
@media (max-width: 568px) {
  .header__burger {
    width: 24px;
    height: 16px;
    flex: 0 0 24px;
  }
}
.header__burger span, .header__burger::after, .header__burger::before {
  content: "";
  height: 3px;
  width: 100%;
  border-radius: 3px;
  background-color: #845186;
  position: absolute;
  left: 0;
  transition: 0.3s;
}
.header__burger::before {
  top: 0;
}
.header__burger span {
  top: 50%;
  translate: 0 -50%;
}
.header__burger::after {
  bottom: 0;
}
.header__burger.active span {
  opacity: 0;
}
.header__burger.active::before {
  rotate: 45deg;
  translate: 0% 270%;
}
@media (max-width: 568px) {
  .header__burger.active::before {
    translate: 0% 230%;
  }
}
.header__burger.active::after {
  rotate: -45deg;
  translate: 0% -270%;
}
@media (max-width: 568px) {
  .header__burger.active::after {
    translate: 0% -200%;
  }
}

.header-services {
  position: absolute;
  top: 100%;
  right: var(--container);
  padding: 40px;
  display: flex;
  flex-direction: column;
  gap: 32px;
  max-width: 1034px;
  background-color: #fff;
  border: 1px solid var(--input-stroke-dark, #dcdcde);
  box-shadow: 0px 1px 2px 0px rgba(23, 23, 23, 0.0509803922);
  border-radius: 12px;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s;
}
@media (max-width: 1024px) {
  .header-services {
    position: unset;
    top: unset;
    right: unset;
    padding: unset;
    gap: unset;
    max-width: unset;
    border: 0;
    border-radius: unset;
    background: unset;
    opacity: 1;
    pointer-events: unset;
    transition: 0;
    width: 100%;
  }
}
.header-services::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 100%;
  left: 0;
  height: 40px;
  width: 100%;
}
@media (max-width: 1024px) {
  .header-services::before {
    display: none;
  }
}
@media (max-width: 1024px) {
  .header-services__title {
    display: none;
  }
}
.header-services__list {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 16px;
}
@media (max-width: 1024px) {
  .header-services__list {
    grid-template-columns: 1fr;
  }
}
.header-services__item {
  padding: 12px 8px;
  display: flex;
  gap: 9px;
  border-radius: 12px;
  background-color: transparent;
  transition: 0.3s;
  color: #323137;
}
@media (hover: hover) {
  .header-services__item:hover {
    background-color: rgba(132, 81, 134, 0.1019607843);
  }
}
.header-services__info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.header-menu {
  display: flex;
  justify-content: space-between;
  flex: 1;
  padding-left: 95px;
}
@media (min-width: 1025px) {
  .header-menu .collapse {
    display: flex !important;
  }
}
@media (max-width: 1024px) {
  .header-menu {
    opacity: 0;
    pointer-events: none;
    transition: 0.3s;
    position: fixed;
    top: var(--header);
    left: 0;
    right: 0;
    width: 100vw;
    height: calc(100vh - var(--header));
    bottom: 0;
    flex-direction: column;
    gap: 32px;
    padding: 40px 48px;
    background-color: white;
    overflow: auto;
  }
  .header-menu.active {
    opacity: 1;
    pointer-events: all;
  }
  .header-menu .header__list {
    flex-direction: column;
    gap: 0;
  }
  .header-menu .header__item {
    flex-direction: column;
    width: 100%;
  }
  .header-menu .header__item .active > img {
    rotate: 180deg;
  }
  .header-menu .header__item > a {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 84px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0%;
    vertical-align: middle;
    border-bottom: 1px solid var(--input-stroke-light, #f5f6f8);
  }
  .header-menu .header__btn {
    flex: 0 0 48px;
    height: 48px;
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0%;
    vertical-align: middle;
  }
}
@media (max-width: 568px) {
  .header-menu {
    padding: 24px 16px;
  }
}

.footer {
  margin-top: var(--sectionMargin);
  overflow: hidden;
  padding-top: 56px;
  background-color: #845186;
}
.footer__upper {
  display: flex;
  padding-bottom: 48px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
}
@media (max-width: 1024px) {
  .footer__upper {
    flex-direction: column;
    gap: 48px;
    border: 0;
  }
}
.footer__left {
  flex: 0 0 382px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (max-width: 1024px) {
  .footer__left {
    text-align: center;
    flex: unset;
    gap: 20px;
    align-items: center;
  }
}
@media (max-width: 568px) {
  .footer__left {
    gap: 48px;
  }
}
.footer__main {
  display: flex;
  flex-direction: column;
  gap: 28px;
}
@media (max-width: 1024px) {
  .footer__main {
    gap: 20px;
  }
}
@media (max-width: 568px) {
  .footer__main {
    gap: 28px;
  }
}
.footer__title {
  color: white;
}
@media (max-width: 1024px) {
  .footer__title {
    margin-bottom: 12px;
  }
}
.footer__contacts {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
@media (max-width: 1024px) {
  .footer__contacts {
    align-items: center;
  }
}
.footer__mail {
  color: white;
  border-bottom: 1px solid white;
  width: fit-content;
}
@media (max-width: 568px) {
  .footer__mail {
    font-weight: 700;
    font-size: 20px;
    line-height: 24px;
    letter-spacing: 0%;
    vertical-align: middle;
  }
}
.footer__phone {
  color: white;
}
@media (max-width: 568px) {
  .footer__phone {
    font-weight: 500;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: 0%;
    vertical-align: middle;
  }
}
.footer__address {
  display: flex;
  flex-direction: column;
  gap: 8px;
  color: white;
}
.footer__address p {
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0%;
}
@media (max-width: 568px) {
  .footer__address p {
    font-size: 12px;
  }
}
.footer__media {
  display: flex;
  gap: 8px;
}
.footer__media a {
  height: 40px;
  width: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 4px;
  border: 1px solid rgba(255, 255, 255, 0.4);
}
.footer__right {
  flex: 1;
}
.footer__items {
  display: flex;
